import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef, ref, withModifiers } from 'vue';

function Checkboxes(props) {
  const Helper = UI.Component.Form.Checkboxes;
  let {
    id,
    name,
    value,
    options,
    disabled,
    readonly,
    onChange,
    customProps
  } = Helper.getProps(props);
  let boxes = options.map(() => ref(null)); // Makes the checkboxes controlled like React.

  const onChangeFactory = index => {
    return () => {
      onChange && onChange(Helper.mutateValue({
        value,
        [boxes[index].value.checked ? 'insert' : 'remove']: options[index].value
      }));
      boxes.some((b, i) => b.value.checked = value.includes(options[i].value));
    };
  };

  let className = UI.joinClass('rcfm-component-fmcheck', Helper.joinModifierClass(props), props.className);
  return _createVNode("div", {
    "id": id,
    "className": className
  }, [options.map((option, index) => _createVNode("label", {
    "className": "rcfm-fmtag",
    "key": option.value
  }, [_createVNode("input", { ...customProps,
    className: 'rcfm-fmtag',
    type: 'checkbox',
    name,
    ref: boxes[index],
    value: option.value,
    checked: value.includes(option.value),
    disabled: disabled || readonly,
    readonly: disabled || readonly,
    onChange: withModifiers(onChangeFactory(index), ['stop'])
  }, null), option.label]))]);
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(Checkboxes, { ...props,
      name: name.value
    }, null);
  }

};