import { guid } from 'rcfm'
import { unref, ref, computed } from 'vue'



// 1. This hook should be injected in <setup> scope.
// 2. If name is not provided, a computed ref contains an unique id is returned.
// 3. To keep reactivity, the provided name should better be a ref object.
export default function useNonblankName(name = '') {
  const uid = ref('')
  return computed(() => {
    const nm = unref(name)
    if (nm) { return nm }
    if (uid.value) { return uid.value }
    return uid.value = guid()
  })
}
