import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Renderer(Properties, {
  slots
}) {
  let {
    props,
    state
  } = Properties;
  let {
    member,
    onAction
  } = UI.Section.Member.getProps(props);

  if (!state) {
    return null;
  }

  const Member = getMemberRenderer(state.value);
  return _createVNode(Member, { ...props,
    onAction,
    key: member.id
  }, {
    default: () => [slots?.default?.()]
  });
} // [TODO] Refactor into Helper?


Renderer.abilities = function (props = {}) {
  let {
    reactive,
    member
  } = UI.Section.Member.getProps(props);
  let type = reactive.get({
    id: member.id,
    prop: 'type'
  });

  if (!type) {
    return false;
  }

  const Member = getMemberRenderer(type);
  return Member.abilities || {};
}; // [TODO] Refactor into Helper?


function getMemberRenderer(memberType = '') {
  const MemberType = Struct.string.capitalize(memberType);
  return UI.use(`Vue.Section.${MemberType}`);
}

export default {
  props: ['reactive', 'member', 'onAction'],

  setup(props, {
    slots
  }) {
    const UseReactive = UI.Vue.composables.useReactive;
    let {
      reactive,
      member
    } = UI.Section.Member.getProps(props);
    let state = UseReactive(reactive, {
      id: member.id,
      prop: 'type'
    });
    return () => _createVNode(Renderer, {
      props,
      state: state.value
    }, {
      default: () => [slots?.default?.()]
    });
  },

  abilities: Renderer.abilities
};