import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';

function Subform(props, {
  slots
}) {
  const Helper = UI.Section.Member;
  const Field = UI.use('Vue.Component.Form.Field');
  const Highlight = UI.use('Vue.Component.Highlight');
  let {
    reactive,
    member
  } = Helper.getProps(props);
  let {
    state
  } = props;
  let {
    label,
    editable
  } = state.values;
  return _createVNode(Field, {
    "className": "rcfm-subform",
    "label": label
  }, {
    default: () => [_createVNode(Highlight, {
      "type": "error",
      "hintPosition": "top",
      "className": "rcfm-subform-container",
      "on": state?.errors ? true : false,
      "hint": Helper.errorMessages({
        reactive,
        member,
        errors: state.errors
      })
    }, {
      default: () => [_createVNode("fieldset", {
        "className": "rcfm-subform-descendants",
        "disabled": !editable
      }, [slots?.default?.()])]
    })]
  });
}

Subform.abilities = {
  WRAP_DESCENDANTS: true
};
export default {
  props: ['reactive', 'member'],

  setup(props, {
    slots
  }) {
    const UseReactive = UI.Vue.composables.useReactive;
    let state = UseReactive(props.reactive, {
      id: props.member.id
    });
    return () => _createVNode(Subform, { ...props,
      state: state.value
    }, {
      default: () => [slots?.default?.()]
    });
  },

  abilities: Subform.abilities
};