import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef, withModifiers } from 'vue';

function TextInput(props) {
  const Helper = UI.Component.Form.TextInput;
  let {
    id,
    name,
    multiline,
    type,
    value,
    placeholder,
    disabled,
    readonly,
    onChange,
    customProps
  } = Helper.getProps(props); // Makes the input controlled like React.
  // [TODO] Improve the controlling behaviour.
  // `e.target.value` should always be set the same as prop `value` in `onInput`
  // Because if `onChange` doesn't change prop `value`,
  // user modified input `value` may not be the same as prop `value`.

  let onInput = e => {
    onChange ? onChange(e.target.value) : e.target.value = value;
  };

  let className = UI.joinClass({
    root: 'rcfm-component',
    klass: multiline ? 'fmtextarea' : 'fminput'
  }, Helper.joinModifierClass(props), props.className);
  return _createVNode("div", {
    "id": id,
    "className": className
  }, [multiline ? _createVNode("textarea", { ...customProps,
    className: 'rcfm-fmtag',
    name,
    value,
    placeholder,
    disabled: readonly || disabled,
    readonly: readonly || disabled,
    onInput,
    onChange: withModifiers(() => {}, ['stop'])
  }, null) : _createVNode("input", { ...customProps,
    className: 'rcfm-fmtag',
    type,
    name,
    value,
    placeholder,
    disabled: readonly || disabled,
    readonly: readonly || disabled,
    onInput,
    onChange: withModifiers(() => {}, ['stop'])
  }, null)]);
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(TextInput, { ...props,
      name: name.value
    }, null);
  }

};