import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef, ref, withModifiers } from 'vue';

function MultipleSelect(props) {
  const Helper = UI.Component.Form.MultipleSelect;
  let {
    id,
    name,
    value,
    options,
    disabled,
    readonly,
    onChange,
    customProps
  } = Helper.getProps(props);
  let select = ref(null); // Makes the options controlled like React.

  let onChangeHandler = () => {
    let opts = Array.from(select.value.options);
    onChange && onChange(options.filter((o, i) => opts[i].selected).map(o => o.value));
    opts.some((o, i) => o.selected = value.includes(options[i].value));
  };

  let className = UI.joinClass('rcfm-component-fmselect', Helper.joinModifierClass(props), props.className);
  return _createVNode("div", {
    "id": id,
    "className": className
  }, [_createVNode("select", { ...customProps,
    className: 'rcfm-fmtag',
    multiple: true,
    name,
    ref: select,
    disabled: disabled || readonly,
    readonly: disabled || readonly,
    onChange: withModifiers(onChangeHandler, ['stop'])
  }, [options.map(option => _createVNode("option", {
    "className": "rcfm-fmtag",
    "key": option.value,
    "value": option.value,
    "selected": value.includes(option.value)
  }, [option.label]))])]);
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(MultipleSelect, { ...props,
      name: name.value
    }, null);
  }

};