import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Editor(props) {
  const Helper = UI.Section.Member;
  let {
    type
  } = props;
  let {
    member,
    onAction
  } = Helper.getProps(props);

  if (!type) {
    return null;
  }

  let TypeCap = Struct.string.capitalize(type.value);
  const Editor = UI.try([`Vue.Section.${TypeCap}.Editor`, 'Vue.Section.Member.Editor.Fallback']);
  return _createVNode(Editor, { ...props,
    onAction,
    key: member.id
  }, null);
}

export default {
  props: ['reactive', 'member'],

  setup(props) {
    const UseReactive = UI.Vue.composables.useReactive;
    let type = UseReactive(props.reactive, {
      id: props.member.id,
      prop: 'type'
    });
    return () => _createVNode(Editor, { ...props,
      type: type.value
    }, null);
  }

};