import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef, ref, withModifiers } from 'vue';

function SingleSelect(props) {
  const Helper = UI.Component.Form.SingleSelect;
  let {
    id,
    name,
    value,
    placeholder,
    includeblank,
    options,
    disabled,
    readonly,
    onChange,
    customProps
  } = Helper.getProps(props);
  let label = Helper.findLabel({
    options,
    value
  });
  let [select, simulator] = [ref(null), ref(null)]; // Makes the options controlled like React.

  let onChangeHandler = () => {
    let index = select.value.selectedIndex - 1;
    onChange && onChange(index < 0 ? null : options[index].value);
    let optionIndex = options.findIndex(o => o.value === value);
    select.value.selectedIndex = optionIndex < 0 ? -1 : optionIndex + 1;
    simulator.value.value = Helper.findLabel({
      options,
      value
    });
  };

  let className = UI.joinClass('rcfm-component-fmselect', Helper.joinModifierClass(props), props.className);
  return _createVNode("div", {
    "id": id,
    "className": className
  }, [_createVNode("select", { ...customProps,
    className: 'rcfm-fmtag',
    name,
    value,
    ref: select,
    disabled: disabled || readonly,
    readonly: disabled || readonly,
    onChange: withModifiers(onChangeHandler, ['stop'])
  }, [_createVNode("option", {
    "className": "rcfm-fmtag",
    "style": {
      display: includeblank ? 'initial' : 'none'
    }
  }, null), options.map(option => _createVNode("option", {
    "className": "rcfm-fmtag",
    "key": option.value,
    "value": option.value
  }, [option.label]))]), _createVNode("input", {
    className: 'rcfm-fmtag',
    tabIndex: -1,
    placeholder,
    value: label,
    ref: simulator,
    disabled: disabled || readonly,
    readonly: disabled || readonly
  }, null)]);
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(SingleSelect, { ...props,
      name: name.value
    }, null);
  }

};