import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Previewer(Properties, {
  slots
}) {
  let {
    props,
    state
  } = Properties;
  let {
    member,
    onAction
  } = UI.Section.Member.getProps(props);

  if (!state) {
    return null;
  }

  const Previewer = UI.try([getPreviewerName(state.value), 'Vue.Section.Member.Previewer.Fallback']);
  return _createVNode(Previewer, { ...props,
    onAction,
    key: member.id
  }, {
    default: () => [slots?.default?.()]
  });
} // [TODO] Refactor into Helper?


Previewer.abilities = function (props = {}) {
  let {
    reactive,
    member
  } = UI.Section.Member.getProps(props);
  let type = reactive.get({
    id: member.id,
    prop: 'type'
  });

  if (!type) {
    return false;
  }

  const PreviewerName = getPreviewerName(type);

  if (!UI.has(PreviewerName)) {
    return {};
  }

  return UI.use(PreviewerName).abilities || {};
}; // [TODO] Refactor into Helper?


function getPreviewerName(memberType = '') {
  const MemberType = Struct.string.capitalize(memberType);
  return `Vue.Section.${MemberType}.Previewer`;
}

export default {
  props: ['reactive', 'member', 'onAction'],

  setup(props, {
    slots
  }) {
    const UseReactive = UI.Vue.composables.useReactive;
    let {
      reactive,
      member
    } = UI.Section.Member.getProps(props);
    let state = UseReactive(reactive, {
      id: member.id,
      prop: 'type'
    });
    return () => _createVNode(Previewer, {
      props,
      state: state.value
    }, {
      default: () => [slots?.default?.()]
    });
  },

  abilities: Previewer.abilities
};