import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { computed } from 'vue';

function Renderer(props) {
  const Helper = UI.Section.Member;
  const MemberTree = UI.use('Vue.View.Form.Tree');
  let reactive = UI.getProp(props, 'reactive', {
    required: true
  });
  let children = props.children ? props.children.value : [];
  return _createVNode("div", {
    "className": "rcfm-form"
  }, [children.map(chd => _createVNode(MemberTree, {
    "key": chd.id,
    "reactive": reactive,
    "member": chd,
    "onAction": (action = {}) => Helper.onAction({ ...action,
      reactive
    })
  }, null))]);
}

export default {
  props: ['reactive'],

  setup(props) {
    const UseReactive = UI.Vue.composables.useReactive;
    let rootId = computed(() => props.reactive.family.root().id);
    let children = UseReactive(props.reactive, {
      id: rootId.value,
      prop: 'children'
    });
    return () => _createVNode(Renderer, { ...props,
      children: children.value
    }, null);
  }

};