import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Action(props) {
  const Helper = UI.Section.Member;
  const Button = UI.use('Vue.Component.Button');
  let {
    reactive,
    member,
    onAction
  } = Helper.getProps(props);
  let type = props.type.value;
  let order = props.order.value;
  let siblings = props.siblings.value;
  let typeDashed = Struct.string.dashCase(type);
  return _createVNode("div", {
    "className": UI.joinClass({
      root: 'rcfm-form-builder',
      klass: ['actions', `${typeDashed}-actions`]
    })
  }, [_createVNode(Button, {
    "className": "rcfm-form-builder-action-delete",
    "circle": true,
    "icon": "cross",
    "color": "danger",
    "size": "small",
    "onClick": () => onAction && onAction({
      action: 'delete',
      reactive,
      id: member.id
    })
  }, null), _createVNode(Button, {
    "className": "rcfm-form-builder-action-decrease",
    "circle": true,
    "icon": "arrow-up",
    "color": "secondary",
    "size": "small",
    "disabled": order === 0,
    "onClick": () => onAction && onAction({
      action: 'update',
      reactive,
      id: member.id,
      prop: 'order',
      value: order - 1
    })
  }, null), _createVNode(Button, {
    "className": "rcfm-form-builder-action-increase",
    "circle": true,
    "icon": "arrow-down",
    "color": "secondary",
    "size": "small",
    "disabled": order === siblings.length - 1,
    "onClick": () => onAction && onAction({
      action: 'update',
      reactive,
      id: member.id,
      prop: 'order',
      value: order + 1
    })
  }, null), _createVNode(Button, {
    "className": "rcfm-form-builder-action-edit",
    "circle": true,
    "icon": "more",
    "color": "secondary",
    "size": "small",
    "onClick": () => onAction && onAction({
      action: 'edit',
      reactive,
      id: member.id
    })
  }, null)]);
}

export default {
  props: ['reactive', 'member'],

  setup(props) {
    const UseReactive = UI.Vue.composables.useReactive;
    let type = UseReactive(props.reactive, {
      id: props.member.id,
      prop: 'type'
    });
    let order = UseReactive(props.reactive, {
      id: props.member.id,
      prop: 'order'
    });
    let siblings = UseReactive(props.reactive, {
      id: props.reactive.family.ancestor({
        id: props.member.id
      }).id,
      //parentId
      prop: 'children'
    });
    return () => _createVNode(Action, { ...props,
      type: type.value,
      order: order.value,
      siblings: siblings.value
    }, null);
  }

};