import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef, ref, withModifiers } from 'vue';

function Radios(props) {
  const Helper = UI.Component.Form.Radios;
  let {
    id,
    name,
    value,
    options,
    disabled,
    readonly,
    onChange,
    customProps
  } = Helper.getProps(props);
  let radios = options.map(() => ref(null)); // Makes the radios controlled like React.

  let onChangeFactory = index => {
    return () => {
      onChange && onChange(options[index].value);
      radios.some((r, i) => r.value.checked = options[i].value === value);
    };
  };

  let className = UI.joinClass('rcfm-component-fmradio', Helper.joinModifierClass(props), props.className);
  return _createVNode("div", {
    "id": id,
    "className": className
  }, [options.map((option, index) => _createVNode("label", {
    "className": "rcfm-fmtag",
    "key": option.value
  }, [_createVNode("input", { ...customProps,
    className: 'rcfm-fmtag',
    type: 'radio',
    name,
    ref: radios[index],
    value: option.value,
    checked: value === option.value,
    disabled: disabled || readonly,
    readonly: disabled || readonly,
    onChange: withModifiers(onChangeFactory(index), ['stop'])
  }, null), option.label]))]);
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(Radios, { ...props,
      name: name.value
    }, null);
  }

};