import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef, withModifiers } from 'vue';

function NumberInput(props) {
  const Helper = UI.Component.Form.NumberInput;
  let {
    id,
    name,
    value,
    placeholder,
    disabled,
    readonly,
    onChange,
    customProps
  } = Helper.getProps(props);
  let formatedValue = isNaN(value) ? '' : value; // Makes the input controlled like React.

  let onInput = e => {
    onChange && onChange(e.target.value.length ? Number(e.target.value) : NaN);
    e.target.value = formatedValue;
  };

  let className = UI.joinClass('rcfm-component-fminput', Helper.joinModifierClass(props), props.className);
  return _createVNode("div", {
    "id": id,
    "className": className
  }, [_createVNode("input", { ...customProps,
    className: 'rcfm-fmtag',
    type: 'number',
    name,
    placeholder,
    value: formatedValue,
    disabled: readonly || disabled,
    readonly: readonly || disabled,
    onInput,
    onChange: withModifiers(() => {}, ['stop'])
  }, null)]);
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(NumberInput, { ...props,
      name: name.value
    }, null);
  }

};