import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Previewer(Properties, {
  slots
}) {
  let {
    props,
    state
  } = Properties;
  const Helper = UI.Section.Member;
  const Highlight = UI.use('Vue.Component.Highlight');
  const Renderer = UI.use('Vue.Section.Member');
  let {
    reactive,
    member,
    onAction
  } = Helper.getProps(props);
  let MemberType = reactive.get({
    id: member.id,
    prop: 'type'
  });
  return _createVNode("div", {
    "className": UI.joinClass(['rcfm-member-previewer', `rcfm-${Struct.string.dashCase(MemberType)}-previewer`])
  }, [_createVNode(Highlight, {
    "type": "error",
    "on": state?.errors ? true : false,
    "hint": Helper.errorMessages({
      reactive,
      member,
      errors: state.errors
    })
  }, {
    default: () => [_createVNode("fieldset", {
      "disabled": true,
      "className": "rcfm-member-previewer-container"
    }, [_createVNode(Renderer, {
      "reactive": reactive,
      "member": member,
      "onAction": onAction
    }, {
      default: () => [slots?.default?.()]
    })])]
  })]);
}

export default {
  props: ['reactive', 'member', 'onAction'],

  setup(props, {
    slots
  }) {
    const UseReactive = UI.Vue.composables.useReactive;
    let {
      reactive,
      member
    } = UI.Section.Member.getProps(props);
    let state = UseReactive(reactive, {
      id: member.id
    });
    return () => _createVNode(Previewer, {
      props,
      state: state.value
    }, {
      default: () => [slots?.default?.()]
    });
  }

};