import { createVNode as _createVNode } from "vue";
import { Struct, I18n } from 'rcfm';
import { UI } from 'rcfm/helper';

function Editor(props) {
  const Helper = UI.Section.Member.Prop;
  const Tabs = UI.use('Vue.Component.Tabs');
  const TabPane = UI.use('Vue.Component.TabPane');
  const Condition = UI.use('Vue.Section.Member.PropEditor.Condition');
  let {
    state
  } = props;
  let {
    reactive,
    member,
    onAction
  } = Helper.getProps(props);
  let type = reactive.get({
    id: member.id,
    prop: 'type'
  });
  let TypeCap = Struct.string.capitalize(type);
  let PropCap = Struct.string.capitalize(member.prop);
  const PropEditor = UI.try([`Vue.Section.${TypeCap}.PropEditor.${PropCap}`, `Vue.Section.Member.PropEditor.${PropCap}`, 'Vue.Section.Member.PropEditor.Simple']);
  let [value, error] = [state.raw.value, state.error ? state.error[0] : null];
  let isSchemable = Helper.isSchemable({
    reactive,
    member
  });
  let isCondition = isSchemable && Helper.isCondition({
    reactive,
    value
  });
  let title = reactive.schema.for(member).label;
  let activeIndex = !isSchemable || !isCondition ? 0 : 1;
  let labels = !isSchemable ? [] : [I18n.global.t('section.member.prop.always'), I18n.global.t('section.member.prop.conditional')];

  let onChange = value => onAction && onAction({ ...member,
    action: 'update',
    reactive,
    value
  });

  let onSwitch = index => onAction && onAction({ ...member,
    action: 'update',
    reactive,
    value: Helper.getDefault({
      reactive,
      member,
      conditional: index && isSchemable
    })
  });

  return _createVNode(Tabs, {
    "className": UI.joinClass(['rcfm-member-prop-editor', `rcfm-${Struct.string.dashCase(TypeCap)}-prop-editor`]),
    "title": title,
    "labels": labels,
    "activeIndex": activeIndex,
    "onSwitch": onSwitch
  }, {
    default: () => [_createVNode(TabPane, null, {
      default: () => [activeIndex === 0 && _createVNode(PropEditor, {
        "reactive": reactive,
        "member": member,
        "value": value,
        "exception": error,
        "onChange": onChange
      }, null)]
    }), _createVNode(TabPane, null, {
      default: () => [activeIndex === 1 && _createVNode(Condition, {
        "reactive": reactive,
        "member": member,
        "value": value,
        "exception": error,
        "onChange": onChange,
        "ResultComponent": PropEditor,
        "defaultResult": Helper.getDefault({
          reactive,
          member
        })
      }, null)]
    })]
  });
}

export default {
  props: ['reactive', 'member'],

  setup(props) {
    const UseReactive = UI.Vue.composables.useReactive;
    let state = UseReactive(props.reactive, props.member);
    return () => _createVNode(Editor, { ...props,
      state: state.value
    }, null);
  }

};