import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Renderer(Properties) {
  const Helper = UI.Section.Field;
  const Field = UI.use('Vue.Component.Form.Field');
  let {
    Component,
    state,
    props
  } = Properties;
  let {
    reactive,
    member,
    onAction
  } = UI.Section.Member.getProps(props);
  let {
    values,
    errors
  } = state;
  return _createVNode(Field, {
    "className": `rcfm-${Struct.string.dashCase(values.type)}`,
    "label": values.label,
    "hint": values.hint,
    "error": errors?.value,
    "required": Helper.isRequired({
      reactive,
      member
    })
  }, {
    default: () => [_createVNode(Component, {
      "name": values.name,
      "value": values.value,
      "invalid": errors ? true : false,
      "readonly": Helper.isReadonly({
        reactive,
        member
      }),
      "placeholder": 'placeholder' in values ? values.placeholder : null,
      "options": 'options' in values ? values.options : null,
      "onChange": value => onAction && onAction({
        action: 'update',
        reactive,
        id: member.id,
        prop: 'value',
        value
      })
    }, null)]
  });
}

export default {
  props: ['Component', 'props'],

  setup(Properties) {
    const UseReactive = UI.Vue.composables.useReactive;
    let {
      reactive,
      member
    } = UI.Section.Member.getProps(Properties.props);
    let state = UseReactive(reactive, {
      id: member.id
    });
    return () => _createVNode(Renderer, { ...Properties,
      state: state.value
    }, null);
  }

};