import { createVNode as _createVNode } from "vue";
import { Struct } from 'rcfm';
import { UI } from 'rcfm/helper';

function Editor(props) {
  const Helper = UI.Section.Member.Prop;
  let {
    type
  } = props;
  let {
    member,
    onAction
  } = Helper.getProps(props);

  if (!type) {
    return null;
  }

  let TypeCap = Struct.string.capitalize(type.value);
  const PropEditor = UI.try([`Vue.Section.${TypeCap}.PropEditor`, 'Vue.Section.Member.PropEditor.Fallback']);
  return _createVNode(PropEditor, { ...props,
    onAction,
    key: `${member.id}/${member.prop}`
  }, null);
}

export default {
  props: ['reactive', 'member'],

  setup(props) {
    const UseReactive = UI.Vue.composables.useReactive;
    let type = UseReactive(props.reactive, {
      id: props.member.id,
      prop: 'type'
    });
    return () => _createVNode(Editor, { ...props,
      type: type.value
    }, null);
  }

};