import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';
import { toRef } from 'vue';

function Editor(props) {
  const Helper = UI.Section.Member.Calculation;
  const Highlight = UI.use('Vue.Component.Highlight');
  const Field = UI.use('Vue.Component.Form.Field');
  const ObjectEditor = UI.use('Vue.Component.Form.Object');
  let name = props.name;
  let {
    reactive,
    member,
    value,
    exception,
    onChange
  } = Helper.getProps(props);
  let {
    placeholder,
    hint
  } = reactive.schema.for(member);
  return _createVNode(Highlight, {
    "type": "error",
    "hint": exception?.message,
    "on": exception ? true : false
  }, {
    default: () => [_createVNode(Field, {
      "hint": hint
    }, {
      default: () => [_createVNode(ObjectEditor, {
        "multiline": true,
        "name": name,
        "placeholder": placeholder,
        "value": value,
        "onChange": onChange
      }, null)]
    })]
  });
}

export default {
  props: ['name'],

  setup(props) {
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    const name = UseNonblankName(toRef(props, 'name'));
    return () => _createVNode(Editor, { ...props,
      name: name.value
    }, null);
  }

};