import { shallowRef, onUnmounted } from 'vue'



function useReactive(reactive, member = {}) {
  member || (member = {})
  let { id, prop } = member
  let state = shallowRef(getProp(reactive, member))
  if (!id) { return state }
  onUnmounted(reactive.subscribe({ id }, () => {
    if (reactive.family.hasnt({ id })) { return }
    state.value = getProp(reactive, prop ? { id, prop } : { id })
  }))
  return state
}

function getProp(reactive, member){
  return reactive.get(member, { include: ['raw', 'error'] })
}



export default useReactive
