import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';

function SubformFieldsetPreviewer(props, {
  slots
}) {
  // [TODO] Validation in setup?
  // const Helper = UI.Section.Member
  // const { reactive, member } = Helper.getProps(props)
  const order = props.order.value;

  if (order !== 0) {
    return null;
  }

  return _createVNode("div", {
    "className": "rcfm-subform-fieldset-previewer"
  }, [slots?.default?.()]);
}

SubformFieldsetPreviewer.abilities = {
  WRAP_DESCENDANTS: true
};
export default {
  props: ['reactive', 'member'],

  setup(props, {
    slots
  }) {
    const UseReactive = UI.Vue.composables.useReactive;
    const order = UseReactive(props.reactive, {
      id: props.member.id,
      prop: 'order'
    });
    return () => _createVNode(SubformFieldsetPreviewer, { ...props,
      order: order.value
    }, {
      default: () => [slots?.default?.()]
    });
  },

  abilities: SubformFieldsetPreviewer.abilities
};