import { createVNode as _createVNode } from "vue";
import { UI } from 'rcfm/helper';

function Editor(props) {
  const Helper = UI.Section.Member.Prop;
  const Highlight = UI.use('Vue.Component.Highlight');
  const Field = UI.use('Vue.Component.Form.Field');
  let {
    reactive,
    member,
    value,
    exception,
    onChange
  } = props;
  let {
    name,
    calculation
  } = props;
  let PropType = Helper.getType({
    reactive,
    member
  });
  const Editor = UI.use(`Vue.Component.Form.${PropType}`);
  let {
    placeholder,
    hint
  } = reactive.schema.for(member);
  let isCalculationDefined = calculation && calculation.raw.value;
  return _createVNode(Highlight, {
    "type": "error",
    "hint": exception?.message,
    "on": exception ? true : false
  }, {
    default: () => [_createVNode(Field, {
      "hint": hint
    }, {
      default: () => [_createVNode(Editor, {
        "readonly": isCalculationDefined ? true : false,
        "name": name,
        "placeholder": placeholder,
        "value": value,
        "onChange": onChange
      }, null)]
    })]
  });
}

export default {
  props: ['reactive', 'member'],

  setup(props) {
    const UseReactive = UI.Vue.composables.useReactive;
    const UseNonblankName = UI.Vue.composables.useNonblankName;
    let name = UseNonblankName();
    let calculation = UseReactive(props.reactive, {
      id: props.member.id,
      prop: 'calculation'
    });
    return () => _createVNode(Editor, { ...props,
      name: name.value,
      calculation: calculation.value
    }, null);
  }

};